<template>
  <button
    @click="addProduct"
    class="add-button"
    style="font-family: 'Monoton';"
  >
    ADD CARD
  </button>
  <button
    @click="toggleInterval"
    class="add-button interval"
    style="font-family: 'Monoton';"
  >
    {{ isIntervalRunning ? "STOP INTERVAL" : "START INTERVAL" }}
  </button>
  <div class="body">
    <CartComponent :cartItems="cartItems"/>
    <CardComponent @delete="deleteProduct" @add-to-cart="addToCart" :products="products">
    </CardComponent>
  </div>
</template>

<script>
import CardComponent from '@/components/CardComponent.vue';
import CartComponent from '@/components/CartComponent.vue';
import pic1 from '../assets/one_pic.webp';
import pic2 from '../assets/two_pic.webp';
import pic3 from '../assets/three_pic.webp';
import pic4 from '../assets/four_pic.webp';
import pic5 from '../assets/five_pic.webp';
import pic6 from '../assets/six_pic.webp';
import pic7 from '../assets/seven_pic.webp';
import pic8 from '../assets/eight_pic.webp';
import pic9 from '../assets/nine_pic.webp';

export default {
  name: 'HomeView',
  data() {
    return {
      active: null,
      counter: 3,
      isIntervalRunning: false,
      intervalId: null,
      products: [
        {
          id: '1',
          picClass: 'pic-1',
          imageSrc: pic1,
          alt: 'img',
          description: 'DESCRIPTION',
          price: '...................',
          name: 'NAME',
          class: 'item-1',
          active: true,
          addToCart: false,
        },
        {
          id: '2',
          picClass: 'pic-2',
          imageSrc: pic2,
          alt: 'img',
          description: 'DESCRIPTION',
          price: '...................',
          name: 'NAME',
          class: 'item-2',
          active: true,
          addToCart: false,
        },
        {
          id: '3',
          picClass: 'pic-3',
          imageSrc: pic3,
          alt: 'img',
          description: 'DESCRIPTION',
          price: '...................',
          name: 'NAME',
          class: 'item-3',
          active: true,
          addToCart: false,
        },
      ],
      images: [pic1, pic2, pic3, pic4, pic5, pic6, pic7, pic8, pic9],
      cartItems: [],
    };
  },
  components: {
    CardComponent,
    CartComponent,
  },
  methods: {
    addProduct() {
      if (this.counter >= 9) {
        this.counter = 0;
      }
      const newProduct = {
        id: `item-${this.counter + 1}`,
        picClass: `pic-${this.counter + 1}`,
        imageSrc: this.images[this.counter],
        alt: 'img',
        description: 'DESCRIPTION',
        price: '...................',
        name: 'NAME',
        class: `item-${this.counter + 1}`,
        active: true,
        addToCart: false,
      };
      this.products.push(newProduct);
      this.counter += 1;
    },
    addToCart(productId) {
      const productObj = this.products.find((product) => product.id === productId);
      /*
      this.products.forEach((product) => {
        if (product.id === productId) {
          productObj.addToCart = false;
          setTimeout(() => {
            productObj.addToCart = true;
          }, 1000);
        }
      });
      */
      const cartItem = { ...productObj };
      this.cartItems.push(cartItem);
      console.log(cartItem);
    },
    deleteProduct(id) {
      this.products = this.products.filter((product) => product.id !== id);
    },
    toggleInterval() {
      if (this.isIntervalRunning) {
        // Interval is running, stop it
        clearInterval(this.intervalId);
        this.isIntervalRunning = false;
      } else {
        // Interval is not running, start it
        this.intervalId = setInterval(() => {
          this.addProduct();
        }, 1000);
        this.isIntervalRunning = true;
      }
    },
  },
  beforeUnmount() {
    clearInterval(this.intervalId);
  },
};
</script>

